import TopicService from '../../services/topic.service';
import './home.style.css';

import {
    Modal,
    Form,
    Input,
    Select,
    Button
} from "antd";

const { Option } = Select;

export default function CreateTopic(props) {
    const { isModalVisible, setIsModalVisible, appendTopic } = props;
    const [form] = Form.useForm();

    const handleOk = () => {
        setIsModalVisible(false);
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };

    const onFinish = (values) => {
        values['tab'] = 1;
        values['status_id'] = 1;
        TopicService.create(values)
            .then(res => {
                const data = res.data;
                appendTopic(data.data);
                handleOk();
                form.resetFields();
            })
            .catch(e => { console.log(e) })

    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <Modal
            title="To  ti mi"
            visible={isModalVisible}
            onOk={handleOk}
            onCancel={handleCancel}
            footer={null}
        >
            <Form
                name="createTopic"
                form={form}
                labelCol={{ span: 8 }}
                wrapperCol={{ span: 16 }}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                autoComplete="off"
            >
                <Form.Item
                    label="Tn gc"
                    name="original_name"
                    // rules={[{ required: true, message: 'Vui lng in tn gc' }]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    label="Tn ting vit"
                    name="vi_name"
                    rules={[{ required: true, message: 'Vui lng in tn ting vit' }]}
                >
                    <Input />
                </Form.Item>

                <Form.Item
                    label="Tt ph"
                    name="short_description"
                >
                    <Input.TextArea />
                </Form.Item>
                <Form.Item
                    label="Tc gi"
                    name="author"
                    // rules={[{ required: true, message: 'Vui lng in tc gi' }]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    label="n v u thc bn quyn"
                    name="copyright_trustee"
                    // rules={[{ required: true, message: 'Vui lng in n v u thc bn quyn' }]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    label="Agency"
                    name="agency"
                    // rules={[{ required: true, message: 'Vui lng in Agency' }]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    label="T kho gi nh"
                    name="keywords"
                    // rules={[{ required: true, message: 'Vui lng in t kho gi nh' }]}
                >
                    <Input placeholder="Mi t kho cch nhau bng du ','" />
                </Form.Item>
                <Form.Item
                    label="Cn i tc dch"
                    name="translation"
                    // rules={[{ required: true, message: 'Vui lng chn bn dch' }]}
                >
                    <Select
                        allowClear
                    >
                        <Option value={true}>C</Option>
                        <Option value={false}>Khng</Option>
                    </Select>
                </Form.Item>
                <Button
                    style={{
                        marginRight: '10px'
                    }}
                    key="back"
                    onClick={handleCancel}
                >
                    Thot
                </Button>
                <Button
                    key="submit"
                    htmlType="submit"
                    type="primary"
                >
                    Lu
                </Button>
            </Form>
        </Modal>
    )
}